import React, { useState } from "react";
import { Col, Row } from "react-bootstrap";
import { connect, useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { AvField, AvForm } from "availity-reactstrap-validation";
import {
  loadingToggleAction,
  loginAction,
} from "../../../store/actions/AuthActions";
import { Translate } from "../../Enums/Tranlate";
import login from "../../../images/login-bg.jpeg";

function Login(props) {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const dispatch = useDispatch();
  const Auth = useSelector((state) => state.auth);
  const lang = useSelector((state) => state.auth.lang);

  function onLogin(e) {
    e.preventDefault();
    dispatch(loadingToggleAction(true));
    dispatch(loginAction(email, password, navigate));
  }

  return (
    <div className={`login-wrapper ${lang}`}>
      <span className="login-border"></span>
      <div className="row w-100">
        <div className="col-md-6 one">
          <div className="position-absolute"
          style={{
            top: "47%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            zIndex: "9",
            textWrap: "nowrap",
            textAlign: 'center'
          }}>
            <h3 className="text-white">Zentra</h3>
            <h3 className="text-white">Welcome to Dashboard</h3>
          </div>
        </div>
        <div className="col-md-6">
          <div className="authincation-content auth-form text-center">
                      {/* <div className=" mb-4">
                        <h3 className="logo-login">Zentra</h3>
                        <h3 className="signIn">{Translate[lang].sign_in}</h3>
                      </div> */}
                      {props.errorMessage && (
                        
                        <div className="text-danger">{props.errorMessage}</div>
                      )}
                      {props.successMessage && (
                        <div className="bg-green-300 text-green-900 border border-green-900 p-1 my-2">
                          {props.successMessage}
                        </div>
                      )}
                      <i className="la la-users text-white mb-3" style={{fontSize: '4rem'}}></i>
                      <AvForm onValidSubmit={onLogin} className="login-form">
                        <Row>
                          <Col md={12} className="form-group mb-2">
                            {/* <label style={{ fontSize: "16px" }}>
                              {Translate[lang].email}
                            </label> */}
                            <AvField
                              name="email"
                              type="email"
                              value={email}
                              errorMessage="Please Enter a Valid Value"
                              className="radius-10"
                              style={{height: "50px"}}
                              validate={{
                                required: {
                                  value: true,
                                  errorMessage: Translate[lang].field_required,
                                },
                              }}
                              placeholder={Translate[lang].email}
                              onChange={(e) => setEmail(e.target.value)}
                            />
                          </Col>
                          <Col md={12} className="form-group position-relative">
                            {/* <label style={{ fontSize: "16px"}}>
                              {Translate[lang].password}
                            </label> */}
                            <AvField
                              name="password"
                              type={showPassword ? "text" : "password"}
                              value={password}
                              className="radius-10"
                              style={{height: "50px"}}
                              errorMessage="Please enter a valid password"
                              validate={{
                                required: {
                                  value: true,
                                  errorMessage: Translate[lang].field_required,
                                },
                              }}
                              placeholder={Translate[lang].password}
                              onChange={(e) => setPassword(e.target.value)}
                            />
                            {showPassword ? (
                              <i
                                className="la la-eye-slash position-absolute cursor-pointer"
                                style={{
                                  top: "16px",
                                  right: lang === "en" ? "8%" : "auto",
                                  left: lang === "ar" ? "8%" : "auto",
                                }}
                                onClick={() => setShowPassword(false)}
                              ></i>
                            ) : (
                              <i
                                className="la la-eye position-absolute cursor-pointer"
                                style={{
                                  top: "16px",
                                  right: lang === "en" ? "8%" : "auto",
                                  left: lang === "ar" ? "8%" : "auto",
                                }}
                                onClick={() => setShowPassword(true)}
                              ></i>
                            )}
                          </Col>
                        </Row>
                        <div className="text-center">
                          <button
                            type="submit"

                            className="btn btn-primary btn-block mb-4 radius-10 h-40"
                            disabled={Auth?.showLoading}
                          >
                            {Translate[lang].login}
                          </button>
                        </div>
                      </AvForm>
                      <div className="text-center mb-2">
                        <label
                          className="poweredBy d-block text-white"
                          style={{ textWrap: "nowrap", fontSize: "12px" }}
                        >
                          Powered by{" "}
                          <a href="#" target="_blank" rel="noreferrer" style={{color: '#fff'}}>
                            Leap Tech kw{" "}
                          </a>
                        </label>
                      </div>
          </div>
        </div>
        </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    errorMessage: state.auth.errorMessage,
    successMessage: state.auth.successMessage,
    showLoading: state.auth.showLoading,
  };
};
export default connect(mapStateToProps)(Login);
